import React,{Component} from 'react';
import { PostCart } from '../Component/PostCart';
import { Post } from '../Component/Post';
import './Profile.css'
import { NavLink } from 'react-router-dom';
import { variables } from '../../Variables';

export class ProfilePro extends Component {
    constructor(props){
        super(props);
        this.state={
            users:[],
            Workers:[],
            Links:[],
            Skills:[],
            AllSkills:[],
            Posts:[],
            ProfileUser:{},
            Is_userlogProfile:false,
            log:this.props.log,
            RateProfile:'?',
            UserRating:[],
            isLoadPosts: false,
            ProfileLinks:[],
            ProfileSkills:[],
            ProfilePosts:[],
            usertbl:"User",
            Workerstbl:"Worker",
            Linkstbl:"LinksProfile",
            Skillstbl:"WorkerMaitrise",
            AllSkillstbl:"Skill",
            Poststbl:"Post",
            slidesPerView:0,

            location:window.location.pathname,
            PhotoPath:variables.PHOTO_URL,
        };
    }
    AnimateRate=(e)=>{
        let Rate=e.target.getAttribute('value-s')
        if(this.state.RateProfile==='?'){
           for(let i=1;i<=5;i++){
            if(i<=Rate) document.getElementById('RatePro'+i).classList.add('checked')
            else document.getElementById('RatePro'+i).classList.remove('checked')
           }
        }
    }
    SelectRate=(e)=>{
        this.state.RateProfile = e.target.getAttribute('value-s');
        document.getElementById('btnRate').classList.add('RateActv')
        document.getElementById('RateStar').setAttribute('style','scale:'+(1+this.state.RateProfile/5))
        document.getElementById('RateNum').setAttribute('style','scale:'+(1+this.state.RateProfile/5))
        for(let i=1;i<=5;i++){
            if(i<=this.state.RateProfile) document.getElementById('RatePro'+i).classList.add('checked')
            else document.getElementById('RatePro'+i).classList.remove('checked')
        }
        this.setState({})
    }
    RateProfile=()=>{
        if(this.state.RateProfile!=='?'){
            if(this.state.UserRating.length==0){
                this.state.ProfileUser.Rating = ((this.state.ProfileUser.Rating*this.state.ProfileUser.Nbr_Rating)+parseInt(this.state.RateProfile))/(this.state.ProfileUser.Nbr_Rating+1);
                this.state.ProfileUser.Nbr_Rating = this.state.ProfileUser.Nbr_Rating+1;
            }else{
                this.state.ProfileUser.Rating = ((this.state.ProfileUser.Rating*this.state.ProfileUser.Nbr_Rating)+parseInt(this.state.RateProfile)-this.state.UserRating[0].Rating)/(this.state.ProfileUser.Nbr_Rating);
            }
            this.state.ProfileUser.Rating = this.state.ProfileUser.Rating.toFixed(2);
            document.getElementById('msg_RatePro').innerHTML='Thanks For Your Opinion, It Is Important For Us';
            fetch(variables.API_URL+this.state.Workerstbl+'Get',{
                method:'PUT',
                headers:{
                    'Accept':'application/json',
                    'Content-Type':'application/json'
                },
                body:JSON.stringify(this.state.ProfileUser)
            })
            .then(res=>res.json())
            .then((result)=>{
                console.log(result)
                this.AddRateworker();
            },(error)=>{alert('Failed');})
            this.setState({})
        }
    }
    AddRateworker=()=>{
        if(this.state.UserRating.length>0){
            this.state.UserRating[0].Rating = this.state.RateProfile;
            fetch(variables.API_URL+'WorkerRateUser',{
                method:'PUT',
                headers:{
                    'Accept':'application/json',
                    'Content-Type':'application/json'
                },
                body:JSON.stringify(this.state.UserRating[0])
            })
            .then(res=>res.json())
            .then((result)=>{
                console.log(result);
                this.setState({})
            },(error)=>{alert('Failed');})
           
        }else{
            let Rate = {Rater:this.props.userlog.id,Rating: this.state.RateProfile,Worker: this.state.ProfileUser.id,}
            fetch(variables.API_URL+'WorkerRateUser',{
                method:'POST',
                headers:{
                    'Accept':'application/json',
                    'Content-Type':'application/json'
                },
                body:JSON.stringify(Rate)
            })
            .then(res=>res.json())
            .then((result)=>{
                console.log(result)
                this.state.UserRating = [result];
                this.setState({})
            },(error)=>{alert('Failed');})
            
        }
    }
    componentDidMount=()=>{
      this.initUser();
      this.intervalId = setInterval((this.checkProfile), 100);
      window.scroll(0,0)
    }
    componentWillUnmount() {
      clearInterval(this.intervalId);
    }
    checkProfile = () => {
       if(this.state.location!== window.location.pathname){
        this.state.location = window.location.pathname;
        this.initUser()
       }
    }
    initUser=()=>{
        ///////////// GET Profile User
        const pathname = window.location.pathname; 
        const Username  = pathname.split('/')[2]

        fetch(variables.API_URL+this.state.usertbl+'Get',{
            method:'POST',
            headers:{
                'Accept':'application/json',
                'Content-Type':'application/json'
            },
            body:JSON.stringify({UserName:Username})
        })
        .then(res=>res.json())
        .then((result)=>{
            this.state.ProfileUser=result[0];

            this.setState({})
            if(this.props.userlog!=null)if(this.state.ProfileUser.UserName==this.props.userlog.UserName)this.state.Is_userlogProfile=true;
            if(this.state.ProfileUser.Is_Worker){
                ///////////////////Get Worker Info
                fetch(variables.API_URL+this.state.Workerstbl+'Get',{
                      method:'POST',
                      headers:{
                          'Accept':'application/json',
                          'Content-Type':'application/json'
                      },
                      body:JSON.stringify(this.state.ProfileUser)
                  })
                  .then(res=>res.json())
                  .then((result)=>{
                        this.state.ProfileUser=result[0];
                      console.log(result);
                      console.log(this.state.ProfileUser)
                      //////////////GET  Links
                      fetch(variables.API_URL+this.state.Linkstbl+'/'+this.state.ProfileUser.id)
                      .then(response=>response.json())
                      .then(data=>{
                          this.state.ProfileLinks = data;
                          console.log(data)
                          this.setState({})
                      })
                      ///////////////GET Skills
                      fetch(variables.API_URL+this.state.AllSkillstbl)
                      .then(response=>response.json())
                      .then(data=>{
                          this.state.AllSkills=data;
                          this.setState({})
                          fetch(variables.API_URL+this.state.Skillstbl+'/'+this.state.ProfileUser.id)
                          .then(response=>response.json())
                          .then(data=>{
                              console.log(data)
                              this.state.ProfileSkills=data;
                              this.setState({})
                          })
                      })
                      ///////////////GET Posts
                      fetch(variables.API_URL+this.state.Poststbl+'Profile'+'/'+this.state.ProfileUser.id)
                      .then(response=>response.json())
                      .then(data=>{
                          this.state.ProfilePosts=data;
                          this.setState({})
                      })
                      if(this.state.log>0)
                      fetch(variables.API_URL+'WorkerRateUser'+'/'+this.props.userlog.id+'/'+this.state.ProfileUser.id)
                      .then(response=>response.json())
                      .then(data=>{
                            this.state.UserRating = data;
                            if(data.length>0)this.state.RateProfile = data[0].Rating;
                          this.setState({})
                      })
                      this.setState({isLoading: false}) 
                  },(error)=>{alert('Failed');})
             }   
        }).catch(error => this.setState({ isLoading: false, error }));
    }

    render(){
        const {
            ProfileUser,
            ProfileLinks,
            ProfileSkills,
            ProfilePosts,
            PhotoPath,
            Is_userlogProfile,
            RateProfile,
            log,
            slidesPerView
        }=this.state;
        return(
            <div className='Profile-back'>
                <br/>
                <br/>
                        <div class="profile-header">
                            <div className='Topprofile'>
                                <div className='profile_pic'>
                                <div>
                                    <img src={PhotoPath+ProfileUser.ProfilePhoto} alt="Profile picture" class="profile-img"/>
                                </div>
                            {!Is_userlogProfile?<>
                                {log==0?<><NavLink className='btn btn3 m-1' to='/Login/1'>Rate</NavLink></>:<>
                                <button className='btn btn3 m-1' data-bs-toggle="modal"data-bs-target={"#RateWorker"}><i class="fa fa-star" aria-hidden="true"></i> Rate</button>
                                <div className="modal fade " id={"RateWorker"} tabIndex="-1" aria-hidden="true">
                                <div className="modal-dialog modal-lg modal-dialog-centered">
                                <div className="modal-content RateSpace">
                                <button className='btn-close m-3' data-bs-dismiss="modal" aria-label="Close" type="submit"></button>
                                <div className="modal-body d-flex flex-column">
                                    <span><i class="fa fa-star "id='RateStar'></i></span>
                                    <span id='RateNum'>{RateProfile}</span>
                                    <h6 className='checked'>Rate this Worker</h6>
                                    <h4>{ProfileUser.UserName}</h4>
                                    <span>{Array.apply(null, { length: 5 }).map((e, i) => (<i className={'bi bi-star-fill h5 starRate'+((i<0)?' checked':null)} id={'RatePro'+(i+1)} value-s={i+1} onClick={this.SelectRate} onMouseOver={this.AnimateRate}></i>))}</span>
                                    <button className='btn RateButton' id='btnRate'onClick={this.RateProfile}>Rate</button>
                                    <div id='msg_RatePro' className='text-info'></div>
                                </div>
                                </div> 
                                </div>
                                </div></>}</>
                                :<div className='text-center'><NavLink className="btn btn3 m-1" to="/Settings/Profile"><i class="bi bi-gear-fill">Account</i></NavLink></div>}
                                </div>
                                <div className="profile-info ms-2">
                                    <h2>{ProfileUser.Name} {ProfileUser.Lastname}</h2>
                                    <h3>{ProfileUser.UserName}</h3>
                                    {ProfileUser.Is_Worker?<>
                                    <div className='h6'>{ProfileUser.Profession} <img src='../PIC/brain.png' width='30px'/></div>
                                    <div>{ProfileUser.Education_Level} </div>
                                    <div className='SliderProfile'>
                                    <div className='m-2 d-flex'>
                                        {ProfileLinks.map((Link, i) => ( <a className='linksProfile' href={Link.URL} target='_blank'><i class="bi bi-link-45deg"></i>{Link.To}</a>))}
                                    </div>
                                    </div>
                                    <div className='SliderProfile'>
                                    <div className='m-2 d-flex'>
                                    {ProfileSkills.map(skill => (<>
                                            <il className='skillsPost d-flex'>
                                                <i className={"bi bi-dice-"+skill.Skill_Rating+"-fill m-1"}></i> 
                                                <span className='m-1'>{skill.Skill.skill}</span>
                                            </il >
                                    </>))}                                        
                                    </div>
                                    </div>
                                    </>:null}
                            <div className='text-light h6'>{ProfileUser.Bio}</div>
                            <div className="profile-stats">
                            {ProfileUser.Is_Worker?<>
                                <div class="stat">
                                    <h3>{ProfilePosts.length}</h3>
                                    <p>Posts</p>
                                </div>
                                
                                <div className="stat">
                                    <div className='d-flex'>
                                        <div className='m-1'>
                                            <h3 className='text-center'><i className={'bi bi-star-fill  checked'}></i>{ProfileUser.Rating}</h3>
                                            <p>Total Rating</p>
                                        </div>
                                        <hr className='hrCreator'/>
                                        <div className='m-1'>
                                            <h3 className='text-center'>{ProfileUser.Nbr_Rating}</h3>
                                            <p>Ratings</p>
                                        </div>
                                    </div>
                                </div>
                                </>:null}
                        </div>
                        </div>
                    </div>
                    
                    {ProfileUser.Is_Worker&&false?<><h2 className='m-3'>Posts</h2></>:null}
                    </div>
                    {/*{ProfileUser.Is_Worker?<>
                   <div className='projects'>
                        {Array.apply(null, { length: 12 }).map((e, i) => (<PostCart/>))}
                    </div>
                    <Post nprevtPIC={this.props.nprevtPIC} nextPIC={this.props.nextPIC} RatePost={this.props.RatePost}/>
                    </>:<div style={{height:'300px'}}></div>}*/}
        </div>
        )
    }
} 
