
export function PostCart({SavePost}){
    return(
        <>
                        <div className="cardPro">
                            <div className="content">
                            <div className="back">
                                <div className="front"data-bs-toggle="modal"data-bs-target="#post">
                                <div className="img">
                                    <img alt='image' src='./PIC/back2.png' className='imgpro'/>
                                </div>
                                <div className="front-content">
                                    <div className='d-flex justify-content-between'>
                                        <h5 className="badge">{'owner'}</h5>
                                        <button className='btn btn-outline-light' SavePost={SavePost}><i class="bi bi-bookmark">5</i></button>
                                    </div>
                                    <div className="description">
                                    <div className="title">
                                        <p className="title"><strong>{'Title'}</strong></p>
                                        <span>{'Date'}</span>
                                    </div>
                                    <p className="cardPro-footer">{'Type'} | {'litl discription'}</p>
                                    <div className='d-flex justify-content-between' id='PostComments'>
                                    {Array.apply(null, { length: 5 }).map((e, i) => (<i className={'bi bi-star-fill '+((i<3)?' checked':null)}></i>))}
                                        <a href='#PostComments' className='btn text-light'><i class="fa-solid fa-comment-dots mt-1"></i></a>
                                    </div>
                                    </div>
                                </div>
                                </div>
                            </div>
                            </div>
                        </div>
        </>
    )
}