
export function Post({RatePost,nprevtPIC,nextPIC,SavePost}){
    return (<>
         <div className="modal fade " id="post" tabIndex="-1" aria-hidden="true">
                <div className="modal-dialog modal-lg modal-dialog-centered">
                <div className="modal-content ">
                <div className="modal-body project">    
                    <div className='infosidepost'>
                    <button type="button" className="btn btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                    <div className='d-flex justify-content-between'>
                        <a className='text-secondary m-3'> <img className='rounded-circle' alt='image' src='PIC/anonymous.png' width='35px' height='35px'/>{' Owner'}</a>
                        <span className='m-1'>{'Date'}</span>
                    </div>
                    <div className="carousel slide d-grid justify-content-center imgecont" data-ride="carousel" >
                        <div class="carousel-inner bg-secondary" id='postcount'>
                            <div id={'PostImgS'+(1)} key-img={1} class="carousel-item activePost">
                            <img class="d-block" src="PIC/back.webp" alt="First slide"/>
                            </div>
                            <div id={'PostImgS'+(2)} key-img={2} class="carousel-item">
                            <img class="d-block" src="..." alt="Second slide"/>
                            </div>
                            <div id={'PostImgS'+(3)} key-img={3} class="carousel-item">
                            <img class="d-block" src="..." alt="Third slide"/>
                            </div>
                        </div>
                        <a class="carousel-control-prev" role="button" onClick={nprevtPIC}>
                            <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                            <span class="sr-only">Previous</span>
                        </a>
                        <a class="carousel-control-next" role="button" onClick={nextPIC}>
                            <span class="carousel-control-next-icon" aria-hidden="true"></span>
                            <span class="sr-only">Next</span>
                        </a>
                        </div>
                        <div className="d-flex justify-content-between m-2">
                        <a href='#PostComments' className='btn text-light'><i class="fa-solid fa-comment-dots mt-1"></i></a>
                        <span>{Array.apply(null, { length: 5 }).map((e, i) => (<i className={'bi bi-star-fill '+((i<3)?' checked':null)}></i>))} {'3.7'}</span>
                        <button className='btn btn-outline-light' SavePost={SavePost}><i class="bi bi-bookmark"></i></button>
                        </div>
                        <details><summary>Links</summary>
                            <ul className='typlist'>
                            {Array.apply(null, { length: 5 }).map((e, i) => (
                                <li><a className='btn typs' href={'#'}>{'Link'+i}</a></li>
                                ))}
                            </ul>
                        </details>
                        <p>Skils list:</p>
                        <div>
                            {Array.apply(null, { length: 15 }).map((e, i) => (
                                <p className=' '></p>
                                ))}
                        </div>
                        <div>
                            {Array.apply(null, { length: 10 }).map((e, i) => ( <a className='linkWS skillsPost ' href='' target='_blank'>{'tagf esfefs'+i}</a>))}
                        </div>                       
                        <details><summary className='button2'><i class="fa fa-star" aria-hidden="true"></i> Rate</summary>
                        <div className='RateSpace'>
                                <h6 className='checked'>Rate this</h6>
                                <h4>Title</h4>
                                <span>{Array.apply(null, { length: 5 }).map((e, i) => (<i className={'bi bi-star-fill starRate'+((i<0)?' checked':null)} id={'Rate'+(i+1)} value-s={i+1} onClick={RatePost}></i>))}</span>
                                <div id='msg_Rate' className='text-info'></div>
                            </div>
                        </details>
                    </div>
                    <div className='sidepost'>
                        <h4 className='text-info'>{'Project Title'}</h4>
                        <span>{'Type'}|{'description g rgoki [pe spokf [esfo mpoeoks[ pfkm pskldf [ekfsa e][oo ksdp fgs jkgskerf[dp k,mfokd kgjew[-eokmvfdp jm okfs[pklfop'}</span>
                    <div className='comments'>
                        <div className='d-flex justify-content-between'>
                                    <i class="fa-solid fa-comment-dots m-2"></i>
                                    <input className='comment' type='text'  placeholder='Comment'/>
                                    <button className='sendbtn'>
                                    <div class="svg-wrapper-1">
                                        <div class="svg-wrapper"><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24"><path fill="none" d="M0 0h24v24H0z"></path><path fill="currentColor" d="M1.946 9.315c-.522-.174-.527-.455.01-.634l19.087-6.362c.529-.176.832.12.684.638l-5.454 19.086c-.15.529-.455.547-.679.045L12 14l6-8-8 6-8.054-2.685z"></path></svg></div>
                                    </div>
                                    <span>Send</span>
                                    </button>
                        </div>
                        <details><summary className='text-info m-2'>read comments</summary>
                        {Array.apply(null, { length: 33 }).map((e, i) => (<div>
                            <a className='text-light m-3'> <img className='rounded-circle' alt='image' src='PIC/anonymous.png' width='35px' height='35px'/>{' commeter'+i}</a>
                            <div className='text-center'>{'GG'}</div>
                            </div>))}
                        </details>
                    </div>
                    </div>
                </div>
                </div> 
                </div>
                </div>
    </>)
}