import React,{Component} from 'react';
import {variables} from '../../Variables.js';
import './Store.css';
import {Slider} from '../Component/slider';
import { Browserstore } from './BrowserStore.js';
import { PostCart } from '../Component/PostCart.js';
import { Post } from '../Component/Post.js';
import { Search } from '../Component/search.js';

export class Store extends Component{
    constructor(props){
        super(props);
        this.state={
            users:[],
            projects:[],
            userlog:{},
            search:'',
            seachList:[],
            usertbl:"",

            PhotoPath:variables.PHOTO_URL,
        };
    }
//////////////////////
FilterFn(){
    var search=this.state.search;

    var filteredData=this.state.projects.filter(
        function(el){
            return el.Title.toString().toLowerCase().includes(
                search.toString().trim().toLowerCase()
            )&&
            el.Owner.toString().toLowerCase().includes(
                search.toString().trim().toLowerCase()
            )
            ////.....
        }
    );
    this.setState({Officier:filteredData});
}
    changesearch=(e)=>{
        this.setState({seach:e.target.value})
        this.FilterFn();
    }
    componentDidMount(){
        this.setState({
            userlog:this.props.userlog,
        })
    }
    selected=(e)=>{
        if(e.target.getAttribute('key-s') === 'N'){
            e.target.setAttribute('key-s','S')
            e.target.style.color='white';
            this.state.seachList.push(e.target.getAttribute('value-s'));
        }else{
            e.target.style.color='';
            e.target.setAttribute('key-s','N')
            let index =this.state.seachList.indexOf(e.target.getAttribute('value-s'));
            if (index > -1) {
                this.state.seachList.splice(index, 1);
              }
        }
        console.log('**')
        this.state.seachList.forEach(e=>{console.log(e)})
    }
    render(){
        const {
            userlog,
            projects,
            search
        }=this.state;
        return(
            <div className='Store d-flex justify-content-end'>
                    <Browserstore selected={this.selected} closeMenu={this.props.closeMenu}/>
                <div className='posts'>
                    <div >
                    <button id='fixbtn' className='typnav btn text-light mt-4' close-t={1} onClick={this.openNavtyp}><i className="bi bi-list m-2  h3"></i></button>
                        <Search search={search} changesearch={this.changesearch}/>
                        <Slider Hist={["hhh"]} select={this.selected}/>
                    </div>
                    <div className='projects'>
                        {Array.apply(null, { length: 12 }).map((e, i) => (<PostCart/>))}
                    </div>
                </div>
                <Post nprevtPIC={this.props.nprevtPIC} nextPIC={this.props.nextPIC} RatePost={this.props.RatePost}/>
            </div>
        )
    }
}
