import React,{Component} from 'react';
import {variables} from '../../Variables';
import { NavWSpace } from './NavWSpace';
import { CartProject } from '../Component/CartProject';
import { Project } from '../Component/Project';
import { Search } from '../Component/search';
import './WorkSpace.css'

export class NewPost extends Component{
    constructor(props){
        super(props);
        this.state={
            newPost:{
                Title:"",
                Description:"",
                Links:[],
                Tags:[],
                Images:[]
            },
            NumLinks:0,
            NumTags:0,
            NumImg:0,
            userlog:{},
            log:this.props.log,
            usertbl:"",

            PhotoPath:variables.PHOTO_URL,
        };
    }
////////////////change
    changeTitle=(e)=>{
        let X=this.state.newPost;
        if(/^[a-zA-Z ]+$/.test(e.target.value)||e.target.value=='')X.Title=e.target.value;
        this.setState({newPost:X})
    }
    changeDescription=(e)=>{
        let X=this.state.newPost;
        X.Description=e.target.value;
        this.setState({newPost:X})
    }
    incNumLinks=()=>{
        let X=this.state.NumLinks;
        X++;
        this.state.newPost.Links.push('')
        this.setState({NumLinks:X})
    }
    decNumLinks=()=>{
        let X=this.state.NumLinks;
        if(X>0)X--;
        this.state.newPost.Links.splice(this.state.NumLinks, 1);
        this.setState({NumLinks:X})
    }
    incNumTags=()=>{
        let X=this.state.NumTags;
        X++;
        this.state.newPost.Links.push('')
        this.setState({NumTags:X})
    }
    decNumTags=()=>{
        let X=this.state.NumTags;
        if(X>0)X--;
        this.state.newPost.Links.splice(this.state.NumTags, 1);
        this.setState({NumTags:X})
    }
    incNumImg=()=>{
        let X=this.state.NumImg;
        X++;
        this.state.newPost.Images.push('')
        this.setState({NumImg:X})
    }
    decNumImg=()=>{
        let X=this.state.NumImg;
        if(X>0)X--;
        this.state.newPost.Images.splice(this.state.NumImg, 1);
        this.setState({NumImg:X})
    }
    changeWorkerjob=(e)=>{
        let X=this.state.newPost;
        let key=e.target.getAttribute('key-w');
        X.Workers[key].job=e.target.value;
        this.setState({newPost:X})
    }
    changeWorkerrequired=(e)=>{
        let X=this.state.newPost;
        let key=e.target.getAttribute('key-w');
        X.Workers[key].required=e.target.value;
        this.setState({newPost:X})
    }
    changeWorkerpaiment=(e)=>{
        let X=this.state.newPost;
        let key=e.target.getAttribute('key-w');
        X.Workers[key].paiment=e.target.value;
        this.setState({newPost:X})
    }
    nprevtPIC=()=>{
        let thispost=document.getElementsByClassName('activePost')[0];
       let prevpost= parseInt(thispost.getAttribute('key-img')) -1;
       if(prevpost>0){
        document.getElementById('PostImg'+prevpost).classList.add('activePost');
        thispost.classList.remove('activePost');
       }
    }
    nextPIC=()=>{
        let thispost=document.getElementsByClassName('activePost')[0];
       let nextpost= parseInt(thispost.getAttribute('key-img')) +1;
       if(nextpost<=document.getElementById('postcount').children.length/2){
        document.getElementById('PostImg'+nextpost).classList.add('activePost');
        thispost.classList.remove('activePost');
       }
    }
    imageUpload=(e)=>{
        e.preventDefault();
        const formData=new FormData();
        formData.append("file",e.target.files[0],e.target.files[0].name);
        /*
        fetch(variables.API_URL+'Photo/savefile',{
            method:'POST',
            body:formData
        })
        .then(res=>res.json())
        .then(data=>{
            this.setState({PhotoFileName:data});
        }) */
    }
    readURL=(e)=>{
        let X=this.state.newPost;
        let key=parseInt(e.target.getAttribute('key-img'));
        X.Images[key-1]=e.target.files[0];
        this.setState({newPost:X});
        if (e.target.files && e.target.files[0]) {
          var reader = new FileReader();
          reader.onload = function (e) {
            document.getElementById(('ImagePost'+key)).setAttribute('src', e.target.result)
          };
          reader.readAsDataURL(e.target.files[0]);
        }
      }
    componentDidMount(){
    }
    render(){
        {
            const {
                userlog,
                log,
                PhotoPath,
                newPost,
                NumLinks,
                NumTags,
                NumImg
            }=this.state;
            return(

                    <div className='ProSW'>
                    <div className='NewProContairer'>
                        <br/><br/>
                    <form onSubmit={null} className='formNewProject'>
                        <h2 className='text-dark text-center'>New Post</h2>
                    <div className='grouPNinput'>
                        <div className="inputNew">
                            <input type="text"  value={newPost.Title} onChange={this.changeTitle} required/>
                            <label htmlFor="app-name-input">Title</label>
                        </div>
                    </div>
                    <div className="inputNew">
                        <textarea type="text" value={newPost.Description} onChange={this.changeDescription} required/>
                        <label>Description</label>
                    </div>
                    <div className='d-flex'>
                        <h4 className='text-dark text-center ms-3 p-1'>Links</h4>
                        <span className=' text-light h6 p-2 ms-1 rounded bg-dark w-fit-content'>{NumLinks}</span>
                        <i className="fa fa-plus text-dark h6 p-2 ms-1 rounded bg-light " aria-hidden="true" onClick={this.incNumLinks}></i>
                        <i className="fa fa-minus text-dark h6 p-2 ms-1 rounded bg-light " aria-hidden="true" onClick={this.decNumLinks}></i>
                    </div>
                    {Array.apply(null, { length: NumLinks }).map((e, i) => (<>
                        <div className='grouPNinput'>
                            <div className="inputNew tallInput">
                                <input type="url" key-w={i} value={newPost.Links[i]} onChange={this.changeWorkerjob} required/>
                                <label>Link {i+1}</label>
                            </div>
                        </div>
                        </>))}
                        <div className='d-flex'>
                        <h4 className='text-dark text-center ms-3 p-1'>Tags</h4>
                        <span className=' text-light h6 p-2 ms-1 rounded bg-dark w-fit-content'>{NumTags}</span>
                        <i className="fa fa-plus text-dark h6 p-2 ms-1 rounded bg-light " aria-hidden="true" onClick={this.incNumTags}></i>
                        <i className="fa fa-minus text-dark h6 p-2 ms-1 rounded bg-light " aria-hidden="true" onClick={this.decNumTags}></i>
                    </div>
                    <div className='Taginput'>
                    {Array.apply(null, { length: NumTags }).map((e, i) => (<>
                            <div className="inputNew" style={{width:'140px'}}>
                                <input type="url" placeholder='' key-w={i} value={newPost.Links[i]} onChange={this.changeWorkerjob} required/>
                                <label>Tag {i+1}</label>
                        </div>
                        </>))}
                        </div>
                        <div className='d-flex'>
                        <h4 className='text-dark text-center ms-3 p-1'>Images</h4>
                        <span className=' text-light h6 p-2 ms-1 rounded bg-dark w-fit-content'>{NumImg}</span>
                        <i className="fa fa-plus text-dark h6 p-2 ms-1 rounded bg-light " aria-hidden="true" onClick={this.incNumImg}></i>
                        <i className="fa fa-minus text-dark h6 p-2 ms-1 rounded bg-light " aria-hidden="true" onClick={this.decNumImg}></i>
                    </div>
                    <div className="carousel slide d-grid justify-content-center w-75 m-auto" style={{background:'#335588'}} data-ride="carousel" >
                        <div className="carousel-inner " id='postcount'>
                    {Array.apply(null, { length: NumImg }).map((e, i) => (<>
                        <div className='d-flex flex-column'>
                        </div>
                           <div id={'PostImg'+(i+1)} key-img={i+1} className={i===0?"carousel-item activePost":"carousel-item"}> 
                            <img className="d-block text-center" id={'ImagePost'+(i+1)}  src='' alt={'Image '+(i+1)} height='300px'/>
                            <label className="custom-file-upload text-center">
                                <input type="file" key-img={i+1}  onChange={this.readURL}/>
                                choose image
                            </label>
                            </div>
                        </>))}
                        </div>
                        <a className="carousel-control-prev" role="button" onClick={this.nprevtPIC}>
                            <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                            <span className="sr-only">Previous</span>
                        </a>
                        <a className="carousel-control-next" role="button" onClick={this.nextPIC}>
                            <span className="carousel-control-next-icon" aria-hidden="true"></span>
                            <span className="sr-only">Next</span>
                        </a>
                        </div>
                    <button className='Workbtn m-3' type="submit">Create Project</button>
                    </form>
                    </div>
                    </div>
            )
    }
}
}